
/* LOGIN - CONTAINER ELEMENTS STYLING */

.login-container {
  width: 300px;
  padding: 1rem;
  background-color: #ff9999;
  margin: 0 auto;
  margin-top: 3rem;
  border-radius: 5px;
  box-shadow: 3px 3px 5px 5px gray;
}

.join-chat {
  display: flex;
  flex-direction: column;

}

.join-chat input {
  margin-bottom: 0.5rem;
  padding: 0.5rem;
}

.join-chat select {
  margin-bottom: 0.5rem;
  padding: 0.5rem;
}

.join-chat button {
  margin-bottom: 0.5rem;
  padding: 0.5rem;
  font-size: larger;
  border: none;
  border-radius: 5rem;
}
.join-chat button:hover {
  background-color: #ff4d4d;
  border: none;
  box-shadow: 3px 3px 3px 3px #4d0000;

}

/* CHAT - CONTAINER - ELEMENTS- STYLING */

.chat-container {
  width: 500px;
  background-color: #ff9999;
  margin: 0 auto;
  margin-top: 3rem;
  border-radius: 5px;
  box-shadow: 3px 3px 5px 5px gray;
}

.chat-container .container-header {
  background-color: black;
  color: white;
  padding: 10px;
}

.chat-container .container-body {
  height: 400px;
  overflow: auto;
}



.chat-container .container-footer {
  display: flex;

}

.chat-container .container-footer input {
  display: flex;
  width: 430px;
  padding: 1rem;
}
.chat-container .container-footer button {
  display: flex;
  padding: 1rem;
}

.message-container {
  padding: 1rem;
}

#you {
  text-align: end;
}

#other {
  text-align: start;
}

#other .actual-message {
  background-color: #adebad;
}

.actual-message {
  padding-left: 10px;
  padding-right: 19px;
  padding-top: 2px;
  padding-bottom: 2px;
  font-size: 20px;
  border-radius: 3px;
  word-wrap: break-word;
  background-color: white;
  text-align: justify;
}

.sender {
  font-weight: bolder;
}





